body {
  font-family: 'Poppins', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #1e3c72, #2a5298);
  margin: 0;
  padding: 20px;
  color: white;
}

.container {
  background: white;
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0px 6px 18px rgba(0, 0, 0, 0.3);
  text-align: center;
  max-width: 500px;
  width: 100%;
}

h1 {
  font-size: 26px;
  color: #333;
  margin-bottom: 25px;
}

.presets {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  justify-content: center;
  margin-bottom: 20px;
}

.preset-btn {
  background: #ff6b6b;
  color: white;
  font-size: 14px;
  padding: 10px 15px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.3s;
}

.preset-btn:hover {
  background: #e63946;
}

.input-group {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 20px;
}

.input-field, select {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 14px;
}

.resize-btn {
  width: 100%;
  background: #0077b6;
  color: white;
  font-size: 16px;
  padding: 12px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.3s;
}

.resize-btn:hover {
  background: #005f8a;
}

.image-preview {
  margin-top: 20px;
  text-align: center;
}

.resized-image {
  max-width: 100%;
  max-height: 250px;
  border-radius: 8px;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.15);
  margin-bottom: 15px;
}

.download-btn {
  background: #2a9d8f;
  color: white;
  font-size: 16px;
  padding: 12px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.3s;
}

.download-btn:hover {
  background: #21867a;
}

.custom-btn {
  background: #f4a261;
}

.custom-btn:hover {
  background: #e76f51;
}
.file-input-container {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  margin-bottom: 15px;
}

.file-input {
  display: none; /* Hide default input */
}

.file-label {
  background: #0077b6;
  color: white;
  font-size: 16px;
  padding: 12px 20px;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.3s;
  display: inline-block;
}

.file-label:hover {
  background: #005f8a;
}
.image-preview {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px; /* Space between image and download button */
  margin-top: 20px;
}

.resized-image {
  max-width: 100%;
  max-height: 300px;
  border-radius: 8px;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.15);
}

.download-btn {
  background: #28a745;
  color: white;
  font-size: 16px;
  border: none;
  border-radius: 8px;
  padding: 12px 20px;
  cursor: pointer;
  transition: 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  width: 180px;
}

.download-btn:hover {
  background: #218838;
}